.spinner-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
  
  .spinner {
    position: relative;
    width: 50px;
    height: 50px;
  }
  
  .spinner::before,
  .spinner::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 60%;
    border: 4px solid #f3f3f3;
    border-top: 4px solid #3fb2cf;
    animation: spin 1s linear infinite;
  }
  
  .spinner::before {
    z-index: 1;
  }
  
  .spinner-logo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  